import React from 'react'
import Carousel from 'react-bootstrap/Carousel';

import styles from "../utils/styles/Carousel.css"

import logo from "./images/prestige2-2.jpg"
import family from "./images/bell-family.jpg"
import crew from "./images/prestige-crew.jpg"
import crew2 from "./images/crew2.jpg"




import Lstyle from "../utils/styles/logo.css"




function carousel() {

    return (

        <div className="Carousel">
            <img src={logo} alt="..." style={Lstyle} id="logo" width={300} height={80} />

            <Carousel>
                <img src={family} alt="..." style={styles} id="pics" />
                <img src={crew} alt="..." style={styles} id="pics" />
                <img src={crew2} alt="..." style={styles} id="pics" />
            </Carousel>
        </div >
    )
}


export default carousel