import React, { Component } from 'react';
import { connect } from 'react-redux';
import { loginUser } from '../actions';
import Navbar from '../utils/Navbar';
import Carousel from '../utils/Carousel'

// import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'


// import axios from 'axios';

import "../utils/styles/login.css"

import Signup from '../utils/Modal';
class LogIn extends Component {

    onLogIn = (e) => {
        e.preventDefault();

        // normally you would pass in the login credentials to the login action
        this.props.loginUser();
    }

    render() {
        return (


            <div className="container">

                <Navbar />
                <div className="row">

                    <div className="col-md-6 offset-md-3">




                        <Carousel />
                        <Signup />

                        <Carousel />
                        {/* <Test /> */}
                        <Button className="btn btn-lg btn-primary btn-block" type="submit" id="button">Log in</Button>



                        <Carousel />

                        <button onClick={this.handleSubmit} className="btn btn-lg btn-primary btn-block" type="submit">Log in</button>

                    </div>
                </div></div>

        );
    }
}

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        error: state.auth.error,
        loading: state.auth.loading
    }
};

export default connect(mapStateToProps, {
    loginUser
})(LogIn);